.Users {
    padding-top: 100px;
    width: 100%;
    height: 100%;
}

.users__wrapper {
    margin: 0 auto;
    width: 95%;
    min-height: 80vh;
    max-height: 100%;
}

.users_inner {
    display: flex;
    overflow: hidden;

    width: 90%;
    min-height: 80vh;
    max-height: 100%;

    margin: 0 auto;
    border-right: rgba(60, 50, 84, 0.5) 1px solid;
    border-left: rgba(60, 50, 84, 0.5) 1px solid;
}

.users_UsersSide {
    display: flex;
    flex-direction: column;
    overflow: hidden;

    width: 70%;
    min-height: 80vh;
    max-height: 100%;
    border-right: rgba(60, 50, 84, 0.5) 1px solid;
}

.users_OptionSide {
    display: flex;
    flex-direction: column;
    overflow: hidden;

    width: 30%;
    min-height: 80vh;
    max-height: 100%;

    padding-top: 10px;
}

.users_options__item {
    width: 90%;
    height: 50px;
    padding-top: 4.5%;
    background-color: #48405c;
    margin: 10px auto;
    text-align: center;
    color: rgb(190, 185, 75);
    font-weight: bold;
    text-transform: uppercase;
    letter-spacing: 3px;
    border-radius: 3px;
}
.users__selectedPage_wrapper{
    width: 10px;
}